@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code&display=swap);
body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F7DAD9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html {
  overflow-y: scroll;
}
/* Document
 * ========================================================================== */

/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */

html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
 * ========================================================================== */

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */

/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */

dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */

ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge 18- and IE.
 */

hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * Add the correct display in IE.
 */

main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
 * ========================================================================== */

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */

b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */

small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */

/**
 * Hide the overflow in IE.
 */

svg:not(:root) {
  overflow: hidden;
}

/* Forms
 * ========================================================================== */

/**
 * Remove the margin on controls in Safari.
 */

button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */

button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */

input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */

legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */

progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */

select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */

textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */

[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */

::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */

:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */

/*
 * Add the correct display in Edge 18- and IE.
 */

details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */

dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */

/**
 * Add the correct display in IE.
 */

template {
  display: none;
}

/* User interaction
 * ========================================================================== */

#root {
    font-family: 'Roboto Condensed', sans-serif;
    --brown-color: #5D534A;
    --white-color:  #FFF5DA;
    --placeholder-color: #5d534aaa;
    color: var(--brown-color);
    display: flex;
    height: 100vh;
}

#app_wrapper {
    max-width: 750px;
    margin: auto;
}

.header {
    font-size: 3rem;
    font-weight: 300;
    text-align: center;
    margin-bottom: 1rem;
}

.header_separator {
    border: none;
    border-top: 2px solid var(--brown-color);
    max-width: 60%;
}
@media (max-width: 700px) {
    #app_wrapper {
        max-width: 95vw;
    }
    .header_separator {
        margin-bottom: 2rem;
    }
}
.guide-line_wrapper {
    display: flex;
    align-items: center;
    margin: 0.5rem 0 0.5rem 0;
}
.guide-line_number {
    font-size: 1.5rem;
    display: flex;
    background-color: var(--white-color);
    text-align: center;
    min-width: 3rem;
    height: 3rem;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-right: 1rem;
    font-weight: 500;

}
.guide-line_description {
    font-size: 1.2rem;
}
.calendar-input {
    width: 100%;
    height: 10rem;
    white-space: nowrap;
    overflow: auto;
    margin: auto;
    box-sizing: border-box;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 3px solid var(--brown-color);
    box-shadow: none;
    -webkit-appearance: none;
    font-size: 1rem;
    outline: none;
    padding: 10px;
    resize: none;
    scrollbar-width: none;
}
.calendar-input::-webkit-scrollbar {
    display: none;
}

.calendar-input:focus {
    border-width: 4px;
    padding: 9px;
}
@media (max-width: 700px) {
    .calendar-input:focus {
        border: 2.5px solid var(--brown-color);
        padding: 9.5px;
    }
}
.class-selector_checkbox {
    display: none;
}
.class-selector_label::before {
    content: '';
    display: inline-block;
    left: 0;
    width: 1.2rem;
    height: 1.2rem;
    border: 2px solid var(--brown-color);
    border-radius: 5px;
    vertical-align: middle;
    margin-right: 10px;
    transition: all 0.06s ease-out;
}
@media (max-width: 700px) {
    .class-selector_label::before {
        width: 1.5rem;
        height: 1.5rem;
    }
}

.calendar-selector_wrapper {
    background-color: var(--white-color);
    border: 3px solid var(--brown-color);
    border-radius: 10px;
    padding: 10px;
    white-space: nowrap;
    overflow-y: auto;
}

.class-selector_label {
   font-size: 1.2rem;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.class-selector_checkbox:checked + .class-selector_label::before {
    background-color: var(--brown-color);
}

.class-item_wrapper {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                   supported by Chrome, Edge, Opera and Firefox */
    margin: 0.25rem;
}

.download-button {
    background-color: var(--white-color);
    font-size: 1.25rem;
    width: 8rem;
    height: 3rem;
    border: 3px solid var(--brown-color);
    border-radius: 15px;
    color: var(--brown-color);
    transition: color 0.2s ease-out;
    font-family: 'Roboto Condensed', sans-serif;
}

.download-button:disabled {
    color: #5d534a69;
    /* border: 3px solid #5d534a69; */
}
.download-button:active {
    background-color: var(--brown-color);
    color: var(--white-color);
}

/* .download-button:hover {
    width: 7.5rem;
    height: 2.5rem;
} */
.file-usage-guide_question {
    background-color: var(--white-color);
    border: 3px solid var(--brown-color);
    border-radius: 10px;
    margin-top: 0.5rem;
    overflow: hidden;
    font-size: 1.2rem;
    min-height: 1.2rem;
    padding: 1rem;
    margin-bottom: 0;
    z-index: 2;
    position: relative;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                   supported by Chrome, Edge, Opera and Firefox */
    
}
.arrow {
    transition: -webkit-transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
}

.point-down {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.question {
    padding-left: 1rem;
}

.file-usage-guide_wrapper {
    position: relative;
    z-index: 2;
}
.file-usage-guide {
    overflow: hidden;
    transition: max-height 1s ease-in-out;
    background-color: var(--white-color);
    border-radius: 10px;
    border: 3px solid var(--brown-color);
    z-index: 1;
    position: relative;
    margin-top: calc(-10px);
}

.file-usage-guide-show {
    transition: max-height 1s ease-in-out; 
    max-height: 800px;
}

.file-usage-guide-hide {
    max-height: 0;
}

i.highlight {
    font-size: 1.1rem;
    font-weight: bold;
}

.link {
    color: var(--brown-color);
    /* text-decoration: none; */
    font-weight: 600;
    transition: all 0.05s ease;
}
.link:visited {
    color: var(--brown-color);
}
.link:active {
    color: var(--white-color);
}

code {
    font-family: 'Fira Code', monospace;
}

.faq-section {
    padding: 0 1rem;
    
}
.faq-section:first-child {
    margin-top: 2rem;
}
.faq-section>p {
    margin-top: 0;
}
.faq-section>h2 {
    margin-bottom: 0.5rem;
}
.footer_wrapper {
    display: flex;
    z-index: 2;
    flex-direction: column;
    align-items: center;
    font-size: 1.2rem;
    text-align: center;
}
.no-underline {
    text-decoration: none;
}
